import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import { isEmpty } from 'lodash'
import styles from './SkillCard.module.scss'
import SkillBar from './SkillBar/SkillBar'

const cx = classNames.bind(styles)

const SkillCard = ({ className, levelNames, skillCategoryName, skills }) => (
  <div
    className={cx('skill-card', className, {
      'skill-card--indent': skillCategoryName,
    })}
  >
    {!isEmpty(levelNames) && (
      <div className={cx('level-names')}>
        {levelNames.map(levelName => (
          <div key={levelName} className={cx('level-names__level-name')}>
            {levelName}
          </div>
        ))}
      </div>
    )}
    {skillCategoryName && (
      <span className={cx('skill-category')}>{skillCategoryName}</span>
    )}
    <div className={cx('skill-bars')}>
      {skills.map(skill => (
        <SkillBar
          key={skill.skillName}
          className={cx('skill-bars__skill-bar')}
          icon={skill.icon}
          skillName={skill.skillName}
          level={skill.level}
        />
      ))}
    </div>
  </div>
)

SkillCard.propTypes = {
  className: PropTypes.string,
  levelNames: PropTypes.arrayOf(PropTypes.string),
  skillCategoryName: PropTypes.string,
  skills: PropTypes.arrayOf(
    PropTypes.shape({
      iconPath: PropTypes.node,
      skillName: PropTypes.string,
      level: PropTypes.number,
    })
  ),
}

SkillCard.defaultProps = {
  className: '',
  levelNames: [],
  skillCategoryName: '',
  skills: [],
}

export default SkillCard
