import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import styles from './About.module.scss'
import { ReactComponent as User } from '../../assets/user.svg'

const cx = classNames.bind(styles)

const About = ({ className, children, slogan }) => (
  <div className={cx('about', className)}>
    <div className={cx('title')}>
      <User className={cx('title__icon')} />
      <span className={cx('title__text')}>About</span>
    </div>

    <div className={cx('content')}>
      {slogan ? <span className={cx('content__slogan')}>{slogan}</span> : null}
      <span className={cx('content__text')}>{children}</span>
    </div>
  </div>
)

About.propTypes = {
  className: PropTypes.string,
  children: PropTypes.node,
  slogan: PropTypes.string,
}

About.defaultProps = {
  className: '',
  children: '',
  slogan: '',
}

export default About
