import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import styles from './Skill.module.scss'

import SkillCard from '../../../components/SkillCard/SkillCard'
import { ReactComponent as LineBars } from '../../../assets/longbar.svg'

const cx = classNames.bind(styles)

const Skill = ({ className, levelNames, skills }) => (
  <div className={cx('skill', className)}>
    <SkillCard
      className={cx('skill-card')}
      levelNames={levelNames}
      skills={skills}
    />
    <LineBars className={cx('decoration-icon')} />
  </div>
)

Skill.propTypes = {
  className: PropTypes.string,
  levelNames: PropTypes.arrayOf(PropTypes.string),
  skills: PropTypes.arrayOf(
    PropTypes.shape({
      iconPath: PropTypes.node,
      skillName: PropTypes.string,
      level: PropTypes.number,
    })
  ),
}

Skill.defaultProps = {
  className: '',
  levelNames: [],
  skills: [],
}

export default Skill
