import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import styles from './SkillClassification.module.scss'

import SkillCard from '../../../components/SkillCard/SkillCard'
import { ReactComponent as Setting } from '../../../assets/setting.svg'

const cx = classNames.bind(styles)

const SkillClassification = ({ className, levelNames, skillCategories }) => (
  <div className={cx('skill-classification', className)}>
    <Setting className={cx('decoration-icon')} />
    <div className={cx('skill-cards')}>
      {skillCategories.map(({ skillCategoryName, skills }, idx) => (
        <SkillCard
          key={skillCategoryName}
          className={cx('skill-card')}
          skillCategoryName={skillCategoryName}
          levelNames={idx === 0 ? levelNames : []}
          skills={skills}
        />
      ))}
    </div>
  </div>
)

SkillClassification.propTypes = {
  className: PropTypes.string,
  levelNames: PropTypes.arrayOf(PropTypes.string),
  skillCategories: PropTypes.arrayOf(
    PropTypes.shape({
      skillCategoryName: PropTypes.string,
      tools: PropTypes.arrayOf(PropTypes.object),
    })
  ),
}

SkillClassification.defaultProps = {
  className: '',
  levelNames: [],
  skillCategories: [],
}

export default SkillClassification
