import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import { animateScroll as scroll } from 'react-scroll'
import styles from './Footer.module.scss'
import { ReactComponent as IconGoToTop } from '../../assets/top.svg'

const cx = classNames.bind(styles)

const Footer = ({ className, worksLink, scrollTop, scrollHeight }) => {
  let goToTopOpacity = Math.floor((scrollTop / scrollHeight) * 100) / 100

  if (goToTopOpacity < 0.1) {
    goToTopOpacity = 0
  } else if (goToTopOpacity < 0.35) {
    goToTopOpacity = 0.35
  }

  return (
    <div className={cx('footer', className)}>
      <a
        className={cx('works')}
        href={worksLink}
        target="_blank"
        rel="noopener noreferrer"
      >
        <span>歡迎至&nbsp;</span>
        <span className={cx('behance')}>Behance</span>
        <span>&nbsp;觀看更多作品與專案</span>
      </a>
      <IconGoToTop
        className={cx('go-to-top')}
        onClick={() => scroll.scrollToTop()}
        style={{ opacity: goToTopOpacity }}
      />
    </div>
  )
}

Footer.propTypes = {
  className: PropTypes.string,
  worksLink: PropTypes.string,
  scrollTop: PropTypes.number,
  scrollHeight: PropTypes.number,
}

Footer.defaultProps = {
  className: '',
  worksLink: '',
  scrollTop: 0,
  scrollHeight: 0,
}

export default Footer
