import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import styles from './SkillAndTool.module.scss'

import Skill from './Skill/Skill'
import SkillClassification from './SkillClassification/SkillClassification'
import { ReactComponent as SkillIcon } from '../../assets/skill.svg'

const cx = classNames.bind(styles)

const SkillAndTool = ({ className, skill, skillClassification }) => (
  <div className={cx('skill-and-tool', className)}>
    <div className={cx('title')}>
      <SkillIcon className={cx('title__icon')} />
      <span className={cx('title__text')}>Skills</span>
    </div>
    <Skill levelNames={skill.levelNames} skills={skill.skills} />
    <SkillClassification
      className={cx('skill-classification')}
      levelNames={skillClassification.levelNames}
      skillCategories={skillClassification.skillCategories}
    />
  </div>
)

SkillAndTool.propTypes = {
  className: PropTypes.string,
  skill: PropTypes.shape({
    levelNames: PropTypes.array,
    skills: PropTypes.array,
  }),
  skillClassification: PropTypes.shape({
    levelNames: PropTypes.array,
    skillCategories: PropTypes.array,
  }),
}

SkillAndTool.defaultProps = {
  className: '',
  skill: [],
  skillClassification: [],
}

export default SkillAndTool
