import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import styles from './Experience.module.scss'

import { ReactComponent as Bag } from '../../assets/bag.svg'
import Task from '../../components/Task/Task'

const cx = classNames.bind(styles)

const Experience = ({ className, jobs }) => (
  <div className={cx('experience', className)}>
    <div className={cx('title')}>
      <Bag className={cx('title__icon')} />
      <span className={cx('title__text')}>Experience</span>
    </div>
    <div className={cx('jobs')}>
      {jobs &&
        jobs.map(({ companyName, title, period, description, img }) => (
          <Task
            key={companyName + period}
            className={cx('jobs__job')}
            taskName={companyName}
            taskRemark={title}
            taskPeriod={period}
            taskDescription={description}
            taskImg={img}
          />
        ))}
    </div>
  </div>
)

Experience.propTypes = {
  className: PropTypes.string,
  jobs: PropTypes.arrayOf(
    PropTypes.shape({
      companyName: PropTypes.string,
      title: PropTypes.string,
      period: PropTypes.string,
      description: PropTypes.string,
    })
  ),
}

Experience.defaultProps = {
  className: '',
  jobs: [],
}

export default Experience
