import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import styles from './Project.module.scss'

import ProjectCard from '../../components/ProjectCard/ProjectCard'
import { ReactComponent as Innovation } from '../../assets/innovation.svg'

const cx = classNames.bind(styles)

const Project = ({ className, projects }) => (
  <div className={cx('project', className)}>
    <div className={cx('title')}>
      <Innovation className={cx('title__icon')} />
      <span className={cx('title__text')}>Project</span>
    </div>
    <div className={cx('projects')}>
      {projects &&
        projects.map(
          ({
            projectName,
            link,
            usedTools,
            subtitle,
            description,
            screenshots,
            video,
          }) => (
            <ProjectCard
              key={projectName}
              className={cx('projects__project')}
              projectName={projectName}
              link={link}
              description={description}
              usedTools={usedTools}
              subtitle={subtitle}
              screenshots={screenshots}
              video={video}
            />
          )
        )}
    </div>
  </div>
)

Project.propTypes = {
  className: PropTypes.string,
  projects: PropTypes.arrayOf(
    PropTypes.shape({
      description: PropTypes.string,
      projectName: PropTypes.string,
      subtitle: PropTypes.string,
      link: PropTypes.string,
      usedTools: PropTypes.arrayOf(PropTypes.string),
      screenshots: PropTypes.arrayOf(PropTypes.string),
      video: PropTypes.string,
    })
  ),
}

Project.defaultProps = {
  className: '',
  projects: [],
}

export default Project
