import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import { range } from 'lodash'
import styles from './SkillBar.module.scss'

import iconMap from '../../../iconMap'
import { skillBarMaxLevel as maxLevel } from '../../../config'

const cx = classNames.bind(styles)
const levels = range(1, maxLevel + 1)

const SkillBar = ({ className, icon, skillName, level }) => {
  const Icon = iconMap[icon]
  return (
    <div
      className={cx('skill-bar', className, {
        'skill-bar--with-icon': icon,
      })}
    >
      <div className={cx('skill-name')}>
        {Icon && <Icon className={cx('skill-name__icon')} />}
        <span className={cx('skill-name__text')}>{skillName}</span>
      </div>
      <div className={cx('level-bar')}>
        {levels.map(idx => (
          <div
            key={idx}
            className={cx(
              'level-bar__level',
              idx <= level
                ? 'level-bar__level--colorful'
                : 'level-bar__level--gray'
            )}
          />
        ))}
      </div>
    </div>
  )
}

SkillBar.propTypes = {
  className: PropTypes.string,
  icon: PropTypes.node,
  skillName: PropTypes.string,
  level: PropTypes.oneOf(levels),
}

SkillBar.defaultProps = {
  className: '',
  icon: '',
  skillName: '',
  level: 1,
}

export default SkillBar
