import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import ReactHtmlParser from 'react-html-parser'
import styles from './Task.module.scss'

import imgMap from '../../imgMap'
import { ReactComponent as Clock } from '../../assets/clock.svg'

const cx = classNames.bind(styles)

const Task = ({
  className,
  isDisplayLine,
  isTitleRowArrangement,
  taskName,
  taskRemark,
  taskPeriod,
  taskDescription,
  taskImg,
}) => (
  <div
    className={cx('task', className, {
      'task--with-line': isDisplayLine,
      'task--title-row-arrangement': isTitleRowArrangement,
    })}
  >
    <div className={cx('task__text')}>
      <div className={cx('task__title')}>
        <div className={cx('brief')}>
          <span className={cx('brief__title')}>{taskName}</span>
          <span className={cx('brief__remark')}>{taskRemark}</span>
        </div>

        {taskPeriod && (
          <div className={cx('period')}>
            <Clock className={cx('period__icon')} />
            <span className={cx('period__text')}>{taskPeriod}</span>
          </div>
        )}
      </div>

      <div className={cx('task__description')}>
        {ReactHtmlParser(taskDescription)}
      </div>
    </div>
    {taskImg && (
      <img className={cx('task__img')} alt="" src={imgMap[taskImg]} />
    )}
  </div>
)

Task.propTypes = {
  className: PropTypes.string,
  taskDescription: PropTypes.string,
  isDisplayLine: PropTypes.bool,
  isTitleRowArrangement: PropTypes.bool,
  taskName: PropTypes.string,
  taskRemark: PropTypes.string,
  taskPeriod: PropTypes.string,
  taskImg: PropTypes.node,
}

Task.defaultProps = {
  className: '',
  taskDescription: '',
  isDisplayLine: false,
  isTitleRowArrangement: false,
  taskName: '',
  taskRemark: '',
  taskPeriod: '',
  taskImg: '',
}

export default Task
