import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import Headroom from 'react-headroom'
import { Link } from 'react-scroll'
import styles from './Header.module.scss'

import imgMap from '../../imgMap'
import { ReactComponent as IconEmail } from '../../assets/email.svg'
import { ReactComponent as IconPhone } from '../../assets/phone.svg'
import { ReactComponent as IconBehance } from '../../assets/behance.svg'

const cx = classNames.bind(styles)

const Header = ({
  className,
  picture,
  name,
  title,
  email,
  phone,
  worksLink,
}) => (
  <div className={cx('header', className)}>
    <Headroom disableInlineStyles>
      <nav className={cx('nav')}>
        <Link className={cx('nav__link')} to="about" smooth duration={500}>
          About
        </Link>
        <Link className={cx('nav__link')} to="education" smooth duration={500}>
          Education
        </Link>
        <Link className={cx('nav__link')} to="experience" smooth duration={500}>
          Experience
        </Link>
        <Link className={cx('nav__link')} to="skill" smooth duration={500}>
          Skill
        </Link>
        <Link className={cx('nav__link')} to="project" smooth duration={500}>
          Project
        </Link>
      </nav>
    </Headroom>

    <div className={cx('profile')}>
      <img className={cx('profile__picture')} src={imgMap[picture]} alt="" />
      <span className={cx('profile__name')}>{name}</span>
      <span className={cx('profile__title')}>{title}</span>
      <div className={cx('profile__contact')}>
        <IconEmail className={cx('profile__icon')} />
        <span className={cx('profile__email')}>{email}</span>
      </div>
      <div className={cx('profile__contact')}>
        <IconPhone className={cx('profile__icon')} />
        <span className={cx('profile__phone')}>{phone}</span>
      </div>
      <a
        className={cx('profile__works')}
        href={worksLink}
        target="_blank"
        rel="noopener noreferrer"
      >
        <IconBehance className={cx('profile__icon')} />
        <span>作品集</span>
      </a>
    </div>
  </div>
)

Header.propTypes = {
  className: PropTypes.string,
  picture: PropTypes.string,
  name: PropTypes.string,
  title: PropTypes.string,
  email: PropTypes.string,
  phone: PropTypes.string,
  worksLink: PropTypes.string,
}

Header.defaultProps = {
  className: '',
  picture: '',
  name: '',
  title: '',
  email: '',
  phone: '',
  worksLink: '',
}

export default Header
