import React, { Component } from 'react'
import classNames from 'classnames/bind'
import ReactHtmlParser from 'react-html-parser'
import { Element } from 'react-scroll'
import styles from './App.module.scss'

import Data from '../data.json'
import Header from '../features/Header/Header'
import About from '../features/About/About'
import Education from '../features/Education/Education'
import Experience from '../features/Experience/Experience'
import SkillAndTool from '../features/SkillAndTool/SkillAndTool'
import Project from '../features/Project/Project'
import Footer from '../features/Footer/Footer'

const cx = classNames.bind(styles)

class App extends Component {
  state = {
    scrollTop: 0,
    scrollHeight: 1,
  }

  componentDidMount() {
    window.addEventListener('scroll', this.setScrollerPos)
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.setScrollerPos)
  }

  setScrollerPos = () => {
    const { scrollTop, scrollHeight } = this.state
    if (
      Math.abs(
        (scrollTop - document.documentElement.scrollTop) / scrollHeight
      ) > 0.05
    ) {
      // eslint-disable-next-line
      this.setState((prevState, props) => ({
        scrollTop: document.documentElement.scrollTop,
        scrollHeight: document.documentElement.scrollHeight,
      }))
    }
  }

  render() {
    const { scrollTop, scrollHeight } = this.state

    const {
      profile,
      about,
      education,
      experience,
      skill,
      skillClassification,
      project,
    } = Data

    return (
      <div className={cx('app')}>
        <Header
          picture={profile.picture}
          name={profile.name}
          title={profile.title}
          email={profile.email}
          phone={profile.phone}
          worksLink={profile.worksLink}
        />
        <div className={cx('content')}>
          <Element name="about">
            <About className={cx('about')} slogan={about.slogan}>
              {ReactHtmlParser(about.description)}
            </About>
          </Element>
          <Element name="education">
            <Education className={cx('education')} schools={education} />
          </Element>
          <Element name="experience">
            <Experience className={cx('experience')} jobs={experience} />
          </Element>
          <Element name="skill">
            <SkillAndTool
              className={cx('skill-and-tool')}
              skill={skill}
              skillClassification={skillClassification}
            />
          </Element>
          <Element name="project">
            <Project className={cx('project')} projects={project} />
          </Element>
        </div>
        <Footer
          worksLink={profile.worksLink}
          scrollTop={scrollTop}
          scrollHeight={scrollHeight}
        />
      </div>
    )
  }
}

export default App
