import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import { isEmpty } from 'lodash'
import ReactHtmlParser from 'react-html-parser'
import styles from './ProjectCard.module.scss'

import iconMap from '../../iconMap'
import imgMap from '../../imgMap'
import { projectLinkText, projectUsedtoolsText } from '../../config'
import { ReactComponent as IconLink } from '../../assets/link.svg'

const cx = classNames.bind(styles)

const ProjectCard = ({
  className,
  projectName,
  link,
  usedTools,
  subtitle,
  description,
  screenshots,
  video,
}) => {
  const icons = usedTools.map(tool => {
    const Icon = iconMap[tool]
    return <Icon key={tool} className={cx('tool')} />
  })

  return (
    <div className={cx('project-card', className)}>
      <div className={cx('header')}>
        <span className={cx('header__project-name')}>{projectName}</span>

        <a
          className={cx('header__link')}
          href={link}
          target="_blank"
          rel="noopener noreferrer"
          data-before={projectLinkText}
        >
          <IconLink className={cx('header__link-icon')} />
        </a>

        <div
          className={cx('header__used-tools')}
          data-before={projectUsedtoolsText}
        >
          {icons}
        </div>
      </div>

      <div className={cx('content')}>
        <span className={cx('content__subtitle')}>{subtitle}</span>
        <div className={cx('content__description')}>
          {ReactHtmlParser(description)}
        </div>
      </div>

      {!isEmpty(screenshots) && (
        <div className={cx('screenshots')}>
          {screenshots.map(pic => (
            <img
              key={pic}
              className={cx('screenshots__screenshot')}
              src={imgMap[pic]}
              alt=""
            />
          ))}
        </div>
      )}

      {video && (
        <iframe
          title="video"
          width="980"
          height="551.25"
          src={video}
          frameBorder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
        />
      )}
    </div>
  )
}

ProjectCard.propTypes = {
  className: PropTypes.string,
  description: PropTypes.string,
  projectName: PropTypes.string,
  subtitle: PropTypes.string,
  link: PropTypes.string,
  usedTools: PropTypes.arrayOf(PropTypes.string),
  screenshots: PropTypes.arrayOf(PropTypes.string),
  video: PropTypes.string,
}

ProjectCard.defaultProps = {
  className: '',
  description: '',
  projectName: '',
  subtitle: '',
  link: '',
  usedTools: [],
  screenshots: [],
  video: '',
}

export default ProjectCard
