import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import styles from './Education.module.scss'

import { ReactComponent as Book } from '../../assets/openbook.svg'
import Task from '../../components/Task/Task'

const cx = classNames.bind(styles)

const Education = ({ className, schools }) => (
  <div className={cx('education', className)}>
    <div className={cx('title')}>
      <Book className={cx('title__icon')} />
      <span className={cx('title__text')}>Education</span>
    </div>
    <div className={cx('schools')}>
      {schools &&
        schools.map(({ schoolName, degree, period, description, img }) => (
          <Task
            key={schoolName + period}
            className={cx('schools__school')}
            isDisplayLine
            isTitleRowArrangement
            taskName={schoolName}
            taskRemark={degree}
            taskPeriod={period}
            taskDescription={description}
            taskImg={img}
          />
        ))}
    </div>
  </div>
)

Education.propTypes = {
  className: PropTypes.string,
  schools: PropTypes.arrayOf(
    PropTypes.shape({
      schoolName: PropTypes.string,
      degree: PropTypes.string,
      period: PropTypes.string,
      description: PropTypes.string,
    })
  ),
}

Education.defaultProps = {
  className: '',
  schools: [],
}

export default Education
